import React, { useState, useEffect } from "react";
import { requestForToken, onMessageListener } from "./firebase";
import useAuth from '../hooks/useAuth'; 


const Notification = () => {
  const [notification, setNotification] = useState({ title: "", body: "" });
  const baseURL = process.env.REACT_APP_API_BASE_URL;
  const { authToken } = useAuth();

  // 
  useEffect(() => {
    const registerServiceWorker = async () => {
      if ('serviceWorker' in navigator) {
        try {
          const registration = await navigator.serviceWorker.register('/firebase-messaging-sw.js');
          console.log('Service Worker registered with scope: ', registration.scope);
        } catch (error) {
          console.error('Service Worker registration failed: ', error);
        }
      } else {
        console.log('Service workers are not supported.');
      }
    };

    registerServiceWorker();
  }, []); // 

  // 
  const handleRequestToken = async () => {
    try {
      const token = await requestForToken();
      console.log('', token);

      if (token) {
        await saveTokenToDatabase(token); // 
      }
    } catch (error) {
      console.error('Failed to get token: ', error);
    }
  };

  // 
  const saveTokenToDatabase = async (token) => {
    console.log('token', JSON.stringify({ subscription_info: token }));
    try {
      const response = await fetch(`${baseURL}/user_notifications/save_subscription_info/`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Token ${authToken}`
        },
        body: JSON.stringify({
          endpoint: token,
          expiration_time: null,
          keys: {
              p256dh: null,
              auth: null,
          },
      }),
      });
      if (response.ok) {
        console.log('Token saved to database');
      } else {
        console.error('Failed to save token');
      }
    } catch (error) {
      console.error('Error saving token:', error);
    }
  };

  // 
  useEffect(() => {
    onMessageListener()
      .then((payload) => {
        setNotification({ title: payload?.notification?.title, body: payload?.notification?.body });
      })
      .catch((err) => console.log("failed: ", err));
  }, []);

  useEffect(() => {
    if (notification?.title) {
      alert("title: " + notification?.title + "\nbody: " + notification?.body);
    }
  }, [notification]);

  return (
    <div>
      <button onClick={handleRequestToken}>Request Notification Permission</button>
    </div>
  );
};

export default Notification;